@import url(https://unpkg.com/ionicons@4.2.2/dist/css/ionicons.min.css);
@import url(https://fonts.googleapis.com/css?family=Big+Shoulders+Display&display=swap);
html {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
nav.responsive-toolbar {
  background-color: #333333;
  height: 60px;
  width: 100%;
  position: fixed; }
  nav.responsive-toolbar ul {
    display: flex;
    -webkit-margin-before: 0;
            margin-block-start: 0;
    -webkit-margin-after: 0;
            margin-block-end: 0;
    -webkit-margin-start: 0;
            margin-inline-start: 0;
    height: 100%;
    align-items: center; }
  nav.responsive-toolbar li {
    list-style-type: none;
    padding: 10px 20px; }
  nav.responsive-toolbar a {
    color: white;
    text-decoration: none;
    font-family: 'Big Shoulders Display', cursive;
    font-size: 1.5rem;
    display: flex;
    flex-direction: row-reverse;
    align-items: center; }
  nav.responsive-toolbar i {
    margin-right: 5px;
    font-size: 1.5rem; }

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

